.header{
  padding:127px 0 0 0;
  margin-bottom: 100px;
  position: relative;
  z-index: 1;
  &__logo{
    width: 433px;
    margin-bottom: 73px;
  }
  &__menu{
    margin:0 124px 0 0;
    padding:0;
    list-style: none;
    display: flex;
    align-items: center;
  }
  &__item{
    margin-right:41px;
    &:last-child{
      margin-right: 0;
    }
    @media screen and (max-width: $sm){
      margin-right:41px;
    }
  }
  &__link{
    color:white;
    font-size: 18px;
    position: relative;
    padding-bottom: 10px;
    &:after{
      content:"";
      position: absolute;
      bottom:0px;
      height:7px;
      width: 100%;
      left: 0;
      background: #189B80;
    }
    &:before{
      content:"";
      position: absolute;
      bottom:0px;
      height:7px;
      width: 0;
      left: 0;
      background: #00FFB0;
      z-index: 1;
      transition:all .2s ease;
    }
    &:hover{
      &:before{
        content:"";
        width: 100%;
      }
    }
  }
}
.top{
  position: relative;
  &:after{
    content:"";
    display: block;
    background: url("../img/pattern.webp");
        background-size: 100%;
    position: absolute;
    top:0;
    bottom:0;
    right:0;
    width:calc(50% - 140px);
  }
  
}
.top-slider{
  max-width: 819px;
  margin-bottom: 200px;
  .swiper-pagination-bullet{
    width: 15px;
    height:15px;
    border:3px solid #189B80;
    background: white;
    opacity: 1;
    &-active{
      border:3px solid #ED344A;
      background: transparent;
    }
  }
  &__item{
    min-height: 308px;
    padding:50px 20px 20px 458px;
    box-sizing:border-box;
    border: 6px solid #189B80;
    background: rgba(12, 69, 82, 0.34);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    margin:0 0 40px;
  }
  &__img{
    width: 403px;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background-size: cover;
    background-position: 50%;
  }
  &__w{
    font-family: 'OpenSans';
    text-transform: uppercase;
    font-size: 11px;
  }
  &__date{
    font-family: 'OpenSans';
    text-transform: uppercase;
    font-size: 25px;
    margin-bottom: 30px;
  }
  &__title{
    font-family: 'Tektur';
    font-size: 25px;
    margin-bottom: 38px;
    b{
      color:#00FFB0;
    }
  }
  &__link{
    font-weight: 700;
    font-size: 15px;
    color:#00FFB0;
    text-decoration: underline;
  }
}

.footer{
  color: white;
  background: #0C4552;
  padding:em(54) 0 em(52);
  .wrap{
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    min-width: 25%;
    @media screen and (max-width: $sm){
      min-width: 50%;
      margin-bottom: em(30);
    }
  }
  &__title{
    font-size: em(25);
    font-weight: 600;
    margin-bottom: em(12);
  }
  &__social{
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
    li{
      min-width: em(40);
      margin-right: em(25);
    }
  }
  &__text{
    font-size: em(18);
    margin-bottom: em(10);
  }
  &__list{
    list-style: none;
    padding: 0;
    margin: 0;
    li{
      padding: 0;
      margin-bottom: em(12);
    }
    a{
      color:white;
      font-size: em(18);
      font-weight: 300;
    }
  }
}