@font-face {
  font-family: 'Latto';
  src: url('../fonts/Latto/regular.woff') format('woff'),
       url('../fonts/Latto/regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Latto';
  src: url('../fonts/Latto/bold.woff') format('woff'),
       url('../fonts/Latto/bold.woff2') format('woff2');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'Tektur';
  src: url('../fonts/Tektur/regular.woff') format('woff'),
       url('../fonts/Tektur/regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Tektur';
  src: url('../fonts/Tektur/medium.woff') format('woff'),
       url('../fonts/Tektur/medium.woff2') format('woff2');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'Tektur';
  src: url('../fonts/Tektur/bold.woff') format('woff'),
       url('../fonts/Tektur/bold.woff2') format('woff2');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'OpenSans';
  src: url('../fonts/OpenSans/regular.woff') format('woff'),
       url('../fonts/OpenSans/regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}
html {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    font-size: 100%;
    height: 100%;
}

body {
    font-family: 'Latto';
    font-weight: 400;
    background: #002E38;
    color: white;
    height: 100%;
    margin: 0;
    padding: 0;
    box-sizing:border-box;
    min-width: 320px;
    @media screen and (max-width: $md) {
    }
    @media screen and (max-width: $sm) {
    }
}

img {
    max-width: 100%;
    pointer-events: none;
}

a {
    text-decoration: none;
    cursor: pointer;
    outline: none;
    color:#7FC9E5;
    &:visited,
    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
        outline: none;
    }
}

.heading{
    font-family: 'Tektur';
    font-size: 37px;
    font-weight:500;
    text-transform:uppercase;
    margin-bottom: 38px;
    span{
      
      &.withmargin{
        display:inline-block;
        margin-left:40px;
      }
      &.mintcolor{
        color:#00FFB0;
      }

    }
}

.wrap {
    min-width: $min-container-width;
    max-width: $max-container-width;
    box-sizing: border-box;
    margin: 0 auto;
    padding: 0 1.5rem;
}
::selection {
    color: white;
    background: #189B80;
}

