@use 'sass:color';
.btn {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    font-size: 16px;
    position: relative;
    border:none;
    transition:all .1s linear;
    font-weight: 400;
    box-sizing: border-box;
    line-height: 1.35em;
    &:hover{
        box-shadow:none;
    }
    &__red{
        background: #ED344A;
        color:white;
        padding:13px 20px;
        position: relative;
        &:hover{
            background: darken(#ED344A, 15%);
            &:before{
                top:-30px;
                background: darken(#ED344A, 15%);
            }
        }
        &:before{
            content:"";
            background: #ED344A;
            display: block;
            width:20px;
            height:20px;
            position: absolute;
            transition:all .1s linear;
            top:-20px;
            right:-20px;
        }
        @media screen and (max-width: $sm){
            padding:11px 20px;
        }
    }
}